import {ChangeDetectorRef, Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {ManagedObject} from "@service/managed-object";
import {VirtualObjectsService} from "@generated/virtual-objects-service";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {DiskManagementService} from "@generated/disk-management-service";
import {Logger} from "@util/logger";
import {VirtualObjectsContext} from "@navigation/context/virtual-objects-context";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {BaseHealthState} from "@util/vsan-health-util";
import {VirtualObjectType} from "@generated/virtual-object-type";
import {KnowledgeBaseId} from '@service/navigation/model/knowledge-base-id';

@Component({
   selector: "vsan-virtual-objects",
   templateUrl: "virtual-objects.html",
   styleUrls: ["virtual-objects.scss"],
})
export class VirtualObjectsComponent implements OnInit {

   readonly KnowledgeBaseId: typeof KnowledgeBaseId = KnowledgeBaseId;

   virtualObjectsloading: boolean = true;

   // Enable Enums for direct use in html template.
   Icon: typeof Icon = Icon;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   // Shows a warning when there are disconnected hosts or network partitioning in the cluster
   showWarning: boolean;

   // data retrieval error
   generalError: string;

   allItems: VirtualObjectModel[];

   objectIdentitiesSupported: boolean = false;
   capabilityCheckLoading: boolean = true;

   presentVirtualObjectUuids: string[];
   initialNameFilter: string;
   initialFilteredTypes: VirtualObjectType[];
   initialFilteredHealthStates: BaseHealthState[];

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(
         private service: VirtualObjectsService,
         private diskMgmtService: DiskManagementService,
         private capabilityProvider: VsanCapabilityProvider,
         private changeDetector: ChangeDetectorRef) {
   }

   ngOnInit(): void {
      this.fetch();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      this.preselectContextData();

      await this.getCapabilities();
      if (!this.objectIdentitiesSupported) {
         return;
      }

      // Start loading the virtual objects data
      try {
         this.virtualObjectsloading = true;
         const clusterRef: ManagedObject = ManagedObject.contextObject;
         [this.allItems, this.showWarning] = await Promise.all([
            this.service.listVirtualObjects(clusterRef, null),
            this.diskMgmtService.hasNetworkPartitionsOrDisconnectedMembers(clusterRef),
         ]);

         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
               VsanUiUtils.getString("vsan.monitor.virtualObjects"));
      } catch (error) {
         this.handleDataError(error);
      } finally {
         this.virtualObjectsloading = false;
         this.changeDetector.detectChanges();
      }
   }

   private preselectContextData(): void {
      const context: VirtualObjectsContext = VsanUiUtils.getNavigationData();
      if (context) {
         this.presentVirtualObjectUuids = context.presentVirtualObjectUuids;
         this.initialNameFilter = context.filterName;
         this.initialFilteredTypes = context.filterTypes;
         this.initialFilteredHealthStates = context.filterHealthStates;
      } else {
         this.presentVirtualObjectUuids = null;
         this.initialNameFilter = null;
         this.initialFilteredTypes = null;
         this.initialFilteredHealthStates = null;
      }
   }

   private async getCapabilities(): Promise<void> {
      this.capabilityCheckLoading = true;
      try {
         this.objectIdentitiesSupported = await this.capabilityProvider
               .getIsObjectIdentitiesSupportedOnCluster(ManagedObject.contextObject);
      } catch (error) {
         this.handleDataError(error);
      }
      this.capabilityCheckLoading = false;
   }

   private handleDataError(error: string): void {
      Logger.debug("Error loading virtual objects data: " + error);
      this.generalError = error;
   }
}
