import {Component, OnInit} from "@angular/core";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {DiskManagementService} from "@generated/disk-management-service";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectsService} from "@generated/virtual-objects-service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {VsanUiUtils} from "@util/vsan-util";
import {PlacementDetailsDialogContext} from "./placement-details-dialog-context";

@Component({
   templateUrl: "placement-details-dialog.html",
   selector: "vsan-placement-details-dialog",
   styleUrls: ["placement-details-dialog.scss"],
})
export class PlacementDetailsDialogComponent implements OnInit {

   clusterRef: ManagedObject;
   virtualObjects: VirtualObjectModel[] = [];
   selectedVirtualObjects: VirtualObjectModel[] = [];
   subtitle: string;
   hasNetworkPartitioning: boolean;

   @LoadingIndicator()
   virtualObjectsLoading: boolean;

   isGrouped: boolean = false;
   selectedVirtualMachines: ManagedObject[] = [];

   // data retrieval error
   @ErrorMessage()
   error: string;

   constructor(private diskMgmtService: DiskManagementService,
         private virtualObjectService: VirtualObjectsService) {
   }

   async ngOnInit(): Promise<void> {
      // Modal window - get the data from modal context
      const context: PlacementDetailsDialogContext = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.virtualObjects = context.virtualObjects;
      this.selectedVirtualObjects = context.selectedVirtualObjects;
      this.isGrouped = context.isGrouped;
      this.selectedVirtualMachines = context.selectedVirtualMachines;
      await this.loadData();
      this.updateSubTitle();
   }

   @Loader({
      srMessage: "vsan.monitor.cluster.virtualObjects.action.viewDetails.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   private async loadData(): Promise<void> {
      const promises: any[] = [];
      if (VsanUiUtils.isEmpty(this.virtualObjects)) {
         const listVirtualObjectsPromise: Promise<void> = this.virtualObjectService
               .listVirtualObjects(this.clusterRef, [])
               .then(result => this.handleVirtualObjects(result));
         promises.push(listVirtualObjectsPromise);
      }
      const hadNetworkPartitionsPromise: Promise<boolean> = this.diskMgmtService
            .hasNetworkPartitionsOrDisconnectedMembers(this.clusterRef)
            .then(result => this.hasNetworkPartitioning = result);
      promises.push(hadNetworkPartitionsPromise);

      await Promise.all(promises);
   }

   private handleVirtualObjects = (result: VirtualObjectModel[]) => {
      if (!result) {
         return;
      }
      this.virtualObjects = this.filterVMs(result);
      this.selectedVirtualObjects = this.getSelectedVirtualObjects();
   };

   private filterVMs(virtualObjects: VirtualObjectModel[]): VirtualObjectModel[] {
      if (!VsanUiUtils.isEmpty(this.selectedVirtualMachines)) {
         return virtualObjects.filter(virtualObject => this.selectedVirtualMachines
               .some(selectedVM => ManagedObject.areEqual(selectedVM, virtualObject.vmRef)));
      }

      return virtualObjects;
   }

   private getSelectedVirtualObjects(): VirtualObjectModel[] {
      const result: VirtualObjectModel[] = [];
      for (const virtualObject of this.virtualObjects) {
         if (!!virtualObject.children) {
            result.push(...virtualObject.children);
         }
      }
      return result;
   }

   private updateSubTitle(): void {
      if (this.selectedVirtualObjects.length === 1) {
         this.subtitle = VirtualObjectsTypeUtil.getDisplayName(this.selectedVirtualObjects[0]);
      } else {
         this.subtitle = VsanUiUtils.getString("vsan.monitor.cluster.virtualObjects.action.viewDetails.subtitle",
               this.selectedVirtualObjects.length);
      }
   }
}
